        margin-bottom: 0.25rem;
        display: block;
    }

    #todo-form-container .cs-title {
        font-size: var(--headerFontSize);
        font-weight: 900;
        line-height: 1.2em;
        text-align: inherit;
        max-width: 43.75rem;
        margin: 0 0 1rem 0;
        color: var(--headerColor);
        position: relative;
    }

    #todo-form-container .cs-text {
