    #todo-form-container .cs-header {
        /* 16px - 20px */
        font-size: clamp(1rem, 2vw, 1.25rem);
        font-weight: 700;
        line-height: 1.2em;
        margin-bottom: 0.5rem;
        color: var(--headerColor);
        display: block;
    }

    #todo-form-container .add-project-button {
        align-self: flex-end;
        background: var(--primary);
        border-radius: 0.2em;
        /* color: var(--color-blue); */
        cursor: pointer;
        font-weight: 700;
        /* line-height: 1em; */
        padding: 0.25em 1.3em;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        top: -53px;
        right: 10px;
        /* width: 50px; */
        height: 34px;
    }


}

/* Tablet - 700px */
@media only screen and (min-width: 43.75rem) {
    #todo-form-container #todo-form {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
    }

    #todo-form-container .cs-label {
        width: 48%;
    }

    #todo-form-container .label-note {
        width: 100%;
    }

    #todo-form-container .cs-button-solid {
        margin-left: 0;
    }

}

/* Small Desktop - 1024px */
@media only screen and (min-width: 64rem) {


    #todo-form-container .cs-container {
        max-width: 32.625rem;
        /* 60px - 108px */
        gap: clamp(0.75rem, 0.9vw, 0.75rem);
        background-color: linen;
    }

    #todo-form-container .cs-label {
        width: 100%;
    }



    .flatpickr-wrapper {
        width: 100%;
    }
}

.todo-home-container {
    /* border: 1px solid red; */
    margin: auto;
    /* width: 50vw; */
    /* height: 100vh; */
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}

.todo-list {
    /* border: 1px solid green; */
    margin: 10px;
    max-width: 90vw;
    /* height: 20px; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.todo-container {
    border: 1px solid blue;
    margin: 4px;
    width: 100%;
    /* height: 40px; */
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all .4s;
    transition: all .4s;
}

.todo-container:hover {
    transform: translateX(6px);
    border-radius: 6px;

}

.todo-content {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    align-items: center;
    /* border: 1px solid green; */

}

.todo-item {
    align-items: center;
    justify-content: center;
    height: 30px;
    padding: 0.3rem;
    margin: 0.5rem;
}

.todo-checkbox {
    display: grid;
    grid-column-start: 1;
    grid-column-end: 2;

    margin-inline: 1rem;
    height: 1.2rem;
    width: 1.2rem;
    border: 2px solid #3ba395;
    cursor: pointer;

}

.todo-checked {
    grid-column-start: 1;
    grid-column-end: 2;
    border-radius: 3px;
    height: 1.2rem;
    width: 1.2rem;
    display: block;
    background: url(https://bscottnz.github.io/todo/img/check.png), #3ba395;
    background-repeat: no-repeat;
    background-size: contain;
}

.todo-title {
    display: flex;
    grid-column-start: 2;
    grid-column-end: 7;
    /* align-items: center; */

    font-size: 1.1rem;
}

.todo-dueDate {
    display: flex;
    align-items: center;
    grid-column-start: 7;
    grid-column-end: 9;
    justify-content: center;
}

.details {
    padding: 5px;
    border: 1px solid green;
    -webkit-transition: all .4s;
    transition: all .4s;
}

.details:hover {
    /* border-width: 2px; */
    border-radius: 4px;
    transform: scale(1.1);
    cursor: pointer;
}

.delete {
    width: 28px;
    height: 28px;
    fill: gray;
    -webkit-transition: all .4s;
    transition: all .4s;
}

.delete:hover {
    fill: var(--priority-high-color);
    transform: scale(1.1);
    cursor: pointer;
}

.priority-low {
    border-left: 3px solid var(--priority-low-color);
}


.priority-medium {
    border-left: 3px solid var(--priority-medium-color);
}


.priority-high {
    border-left: 3px solid var(--priority-high-color);
}